import AddPlayers from './AddPlayers';


const CreateGame = ({setPlayersAdded, inputFields, setInputFields, createGame, setCreateGame}) => {

	const handleCreate = (e) => {
		setCreateGame(true)
	}

	const handleSubmit = (e) => {
		e.preventDefault();
		setPlayersAdded(true)
	}

	return (
		<div>
			{!createGame ?
				<div className='home-container-create-game'>
					<h3>New game</h3>
					<p>Select create game to start entering player names.</p>
					<div className='home-button-container'>
						<button onClick={handleCreate}>
							Create Game
						</button>		
					</div>
				</div>
				:
				<AddPlayers handleSubmit={handleSubmit} inputFields={inputFields} setInputFields={setInputFields}/>
			}
		</div>
	)
}

export default CreateGame