import {FiDelete } from 'react-icons/fi'


const ChangeScoreButtons = ({changeScoreValues, setchangeScoreValues, scoreSelected, setScoreSelected}) => {

	const handleScoreSelected = (value) => {
		setScoreSelected(false)
		changeScoreValues['selectedValue'] = value
	}

	const handleDelete = () => {
    const changeValues = changeScoreValues
    changeValues['selectedValue'] = ''
    setchangeScoreValues(changeValues)
    setScoreSelected(false)
	}

	// Array of all different types of buttons
	const buttons = []

	for (let i=0; i < 31; i++) { 
		buttons.push(<button key={i} onClick={() => handleScoreSelected(i)}>{i}</button>);
	}

	const restOfButtons = [
		<button key="40" onClick={() => handleScoreSelected(40)}>40</button>,
	  <button key="50" onClick={() => handleScoreSelected(50)}>50</button>,
		<button key="100" onClick={() => handleScoreSelected(100)}>100</button>,
		<button key="200" onClick={() => handleScoreSelected(200)}>200</button>,
		<button key="300" onClick={() => handleScoreSelected(300)}>300</button>,
		<button key="400" onClick={() => handleScoreSelected(400)}>400</button>,
		<button key="500" onClick={() => handleScoreSelected(500)}>500</button>,
	]

	for (let i=0; i < restOfButtons.length; i++) {
		buttons.push(restOfButtons[i]);
	}


	const selectedVAlueButtons = {
		'ones': [buttons[0], buttons[1], buttons[2], buttons[3], buttons[4], buttons[5]],
		'twos': [buttons[0], buttons[2], buttons[4], buttons[6], buttons[8], buttons[10]],
		'threes': [buttons[0], buttons[3], buttons[6], buttons[9], buttons[12], buttons[15]],
		'fours': [buttons[0], buttons[4], buttons[8], buttons[12], buttons[16], buttons[20]],
		'fives': [buttons[0], buttons[5], buttons[10], buttons[15], buttons[20], buttons[25]], 
		'sixes': [buttons[0], buttons[6], buttons[12], buttons[18], buttons[24], buttons[30]],
		'threeOfAKind': [buttons[0], buttons.slice(5,31)],
		'fourOfAKind': [buttons[0], buttons.slice(5,31)],
		'fullHouse': [buttons[0], buttons[25]],
		'smStraight': [buttons[0], buttons[30]],
		'lgStraight': [buttons[0], buttons[31]],
		'yahtzee': [buttons[0], buttons[32]], 
		'chance': [buttons[0], buttons.slice(5,31)],
		'yahtzeeBonus': [buttons.slice(33,38)]
	}

	const correctScores = selectedVAlueButtons[changeScoreValues.requiredValue]


	return (
    <div className="changeScore-body-buttons-container">
    	{correctScores}
      <button className='deleteButtonScoreChange' onClick={handleDelete} ><FiDelete /></button>
    </div>
	)
}

export default ChangeScoreButtons