
import { GrTrophy } from 'react-icons/gr'

const Results = ({inputFields,setInputFields,replaySamePlayers,setReplaySamePlayers,setShowResults,setGameComplete,setPlayersAdded,setCreateGame}) => { 

	const handleSubmit = () => {
		if (replaySamePlayers === false) {
			setInputFields(
				[{
					name:"",ones:"",twos:"", threes:"", fours:"",fives:"",sixes:"",threeOfAKind:"",
					fourOfAKind:"",fullHouse:"",smStraight:"",lgStraight:"",yahtzee:"",chance:"",yahtzeeBonus:"",
				}]
			)
			setPlayersAdded(false)
		} else {
			const currentInputFields = [...inputFields]
			
			for (let i=0; i < currentInputFields.length; i++) {
				currentInputFields[i].ones = ""
				currentInputFields[i].twos = ""
				currentInputFields[i].threes = ""
				currentInputFields[i].fours = ""
				currentInputFields[i].fives = ""
				currentInputFields[i].sixes = ""
				currentInputFields[i].threeOfAKind = ""
				currentInputFields[i].fourOfAKind = ""
				currentInputFields[i].fullHouse = ""
				currentInputFields[i].smStraight = ""
				currentInputFields[i].lgStraight = ""
				currentInputFields[i].yahtzee = ""
				currentInputFields[i].chance = ""
				currentInputFields[i].yahtzeeBonus = ""
			}
		}
		setShowResults(false)
		setGameComplete(false)
		setCreateGame(false)
		setReplaySamePlayers(true)
	}

	const handleChange = () => {
		if (replaySamePlayers === true) {
			setReplaySamePlayers(false)
		} else {
			setReplaySamePlayers(true)
		}
	}

	const checkBonus = (ones, twos, threes, fours, fives, sixes) => {
		if (ones+twos+threes+fours+fives+sixes >= 63) {
			return (35)
		} else {
			return (0)
		}
	}

	const getPlayerTotals = (input) => {
		const players = []
		for (let i=0; i < inputFields.length; i++) {
			let player = {}
			let field = inputFields[i]
			player['name'] = inputFields[i]['name']

			var playerTotal = 
				field['ones'] + 
				field['twos'] + 
				field['threes'] + 
				field['fours'] +
				field['fives'] +
				field['sixes'] +
				field['threeOfAKind'] +
				field['fourOfAKind'] +
				field['fullHouse'] +
				field['smStraight'] +												
				field['lgStraight'] +
				field['yahtzee'] +
				field['chance'] +
				field['yahtzeeBonus']

			var numPlayerTotal = Number(playerTotal)
			var bonus = checkBonus(field['ones'], field['twos'], field['threes'], field['fours'], field['fives'], field['sixes'])
			player['total'] = numPlayerTotal + bonus
			players.push(player)
		}

		players.sort((a, b) => {
    	return b.total - a.total 
		});

		if (input === 'name') {
			return (`${players[0].name}`)
		} else if (input === 'total') {
			return (`${players[0].total}`)
		} else {
			return (
				<tbody>
					{players.map((player) => (
						<tr key={player.name}>
							<th>{player.name}</th><td>{player.total} points</td>
						</tr>
					))}
				</tbody>
			)
		}
	}

	return (
		<div style={{textAlign:'center'}}>
			<GrTrophy size='8vh' style={{marginTop:'20px'}}/>
			<div className="winnerNamePoints" style={{marginBottom:'30px'}}>
				<h3>Winner:</h3>
				<h3>
					<span className='winnerDetails'>{getPlayerTotals('name')}</span> with <span>{getPlayerTotals('total')}</span> points!
				</h3>
			</div>
			<div className="resultsTableContainer">
				<table className="resultsTable">
					{getPlayerTotals('scores')}
				</table>
			</div>

			<div>
				<label className="switch" >
			  	<input type="checkbox" defaultChecked onChange={handleChange}/>
			  	<span className="slider round" ></span>
				</label>
				<span className="replayPlayersText">Replay with the same players</span>
			</div>
			<div className='results-button-container'>
				<button type="submit" onClick={handleSubmit}>New game</button>
			</div>
		</div>
	)
}

export default Results


