import {useState} from 'react'
import ScoreValues from './ScoreValues';
import ChangeScoreDisplay from './ChangeScoreDisplay';
import Subtotal from './Subtotal';
import Bonus from './Bonus';
import Total from './Total';
import ConfirmComplete from './ConfirmComplete';

const Scoreboard = ({inputFields, setInputFields, gameComplete, setGameComplete, setShowResults}) => {
	const [changeScoreDisplay, setChangeScoreDisplay] = useState(false);
	const [changeScoreValues, setchangeScoreValues] = useState({name:'', titleValue:'', index:'', requiredValue:'', selectedValue:''});

	const requiredValuesTitleValues = 
	[
		['ones','Ones'],
		['twos','Twos'],
		['threes', 'Threes'],
		['fours','Fours'],
		['fives', 'Fives'],
		['sixes', 'Sixes'],
		['threeOfAKind', '3 of a Kind'],
		['fourOfAKind','4 of a Kind'],
		['fullHouse', 'Full House'],
		['smStraight', 'Small Straight'],
		['lgStraight', 'Large Straight'],
		['yahtzee', 'Yahtzee'], 
		['chance', 'Chance'],
		['yahtzeeBonus', 'Yahtzee Bonus']
	]


	const players = []

	for (let i=0; i < inputFields.length; i++) {
		players.push(<th className="playerNames">{inputFields[i].name}</th>)
	}


	const values = []

	for (let i=0; i < requiredValuesTitleValues.length; i++) {
		values.push(
  		<ScoreValues 
  			key={[i][0]}
  			requiredValue= {requiredValuesTitleValues[i][0]}
  			titleValue = {requiredValuesTitleValues[i][1]}
  			inputFields={inputFields}
  			changeScoreDisplay={changeScoreDisplay}
  			setChangeScoreDisplay={setChangeScoreDisplay} 
  			changeScoreValues={changeScoreValues} 
  			setchangeScoreValues={setchangeScoreValues}
  		/>
		)
	}

	values.splice(6, 0, <Subtotal key='Subtotal' inputFields={inputFields}/>)
	values.splice(7, 0, <Bonus key='Bonus' inputFields={inputFields}/>)
	values.push(<Total key='Total' inputFields={inputFields}/>)



	return (
		<div className="table-container">
	  	{changeScoreDisplay && 
	  		<ChangeScoreDisplay 
	  			setChangeScoreDisplay={setChangeScoreDisplay} 
	  			changeScoreValues={changeScoreValues} 
	  			setchangeScoreValues={setchangeScoreValues}
	  			inputFields={inputFields}
	  			setInputFields={setInputFields}
	  			setGameComplete={setGameComplete}
	  		/>
	  	}
		  {gameComplete && 
		  	<ConfirmComplete gameComplete={gameComplete} setGameComplete={setGameComplete} setShowResults={setShowResults}/>
		  }
	    <table>
	    	<thead>
	    		<tr> 
	    			<th className="titleValues" style={{backgroundColor:'white'}}></th>{players}
	    		</tr>
	      </thead>
	    	<tbody>
	    		{values}
	    	</tbody>
		  </table>
		</div>
	)
}

export default Scoreboard