
const ScoreValues = ({requiredValue, titleValue, inputFields, changeScoreDisplay, setChangeScoreDisplay, changeScoreValues, setchangeScoreValues}) => {

	const handleClick = (index, requiredValue) => {
		setChangeScoreDisplay(true);
		const values = changeScoreValues
		values['name'] = inputFields[index]['name']
		values['titleValue'] = titleValue
		values['index'] = index
		values['requiredValue'] = requiredValue
		setchangeScoreValues(values);
	}


	return (
		<tr>
			<th className="titleValues">{titleValue}</th>
			{inputFields.map((input, index) => (
				<td 
					style={{ cursor: 'pointer' }}
					key={index}
					onClick={() => handleClick(index, requiredValue)}
					>
					{input[requiredValue]}
				</td>
			))}
		</tr>
	)
};

export default ScoreValues