import React from 'react'

const Total = ({inputFields}) => {

	const checkBonus = (ones, twos, threes, fours, fives, sixes) => {
		if (ones+twos+threes+fours+fives+sixes >= 63) {
			return (35)
		} else {
			return (0)
		}
	}

	return (
		<tr>
			<th className="titleValues">Total</th>
			{inputFields.map((input, index) => (
				<td
					key={index}
					>
					{
						(inputFields[index]['ones'] === '' ? 0 : inputFields[index]['ones']) +
						(inputFields[index]['twos'] === '' ? 0 : inputFields[index]['twos']) +
						(inputFields[index]['threes'] === '' ? 0 : inputFields[index]['threes']) +
						(inputFields[index]['fours'] === '' ? 0 : inputFields[index]['fours']) +
						(inputFields[index]['fives'] === '' ? 0 : inputFields[index]['fives']) +
						(inputFields[index]['sixes'] === '' ? 0 : inputFields[index]['sixes']) +
						(inputFields[index]['threeOfAKind'] === '' ? 0 : inputFields[index]['threeOfAKind']) +
						(inputFields[index]['fourOfAKind'] === '' ? 0 : inputFields[index]['fourOfAKind']) +
						(inputFields[index]['fullHouse'] === '' ? 0 : inputFields[index]['fullHouse']) + 
						(inputFields[index]['smStraight'] === '' ? 0 : inputFields[index]['smStraight']) +
						(inputFields[index]['lgStraight'] === '' ? 0 : inputFields[index]['lgStraight']) +
						(inputFields[index]['yahtzee'] === '' ? 0 : inputFields[index]['yahtzee']) +
						(inputFields[index]['chance'] === '' ? 0 : inputFields[index]['chance']) +
						(inputFields[index]['yahtzeeBonus'] === '' ? 0 : inputFields[index]['yahtzeeBonus']) + 

						checkBonus(
							(inputFields[index]['ones'] === '' ? 0 : inputFields[index]['ones']),
							(inputFields[index]['twos'] === '' ? 0 : inputFields[index]['twos']),
							(inputFields[index]['threes'] === '' ? 0 : inputFields[index]['threes']),
							(inputFields[index]['fours'] === '' ? 0 : inputFields[index]['fours']),
							(inputFields[index]['fives'] === '' ? 0 : inputFields[index]['fives']),
							(inputFields[index]['sixes'] === '' ? 0 : inputFields[index]['sixes'])
						)
					}
				</td>
			))}
		</tr>
	)
}

export default Total