//import {useState} from 'react'
import { FaTimes } from 'react-icons/fa'
import { FcPlus } from 'react-icons/fc'

const AddPlayers = ({handleSubmit, inputFields, setInputFields}) => {
	

	const handleChange = (e, index) => {
		const values = [...inputFields];
		values[index][e.target.name] = e.target.value;
		setInputFields(values);
	};


	const handleAdd = () => {
		setInputFields([...inputFields, {
			name:"",
			ones:"",
			twos:"",
			threes:"",
			fours:"",
			fives:"",
			sixes:"",
			threeOfAKind:"",
			fourOfAKind:"",
			fullHouse:"",
			smStraight:"",
			lgStraight:"",
			yahtzee:"",
			chance:"",
			yahtzeeBonus:"",
		}]);
	}

	const handleRemove = (index) => {
		const values = [...inputFields];
		values.splice(index, 1);
		setInputFields(values)
	}


	return (
		<div className="addPlayersContainer">
			<p style={{fontWeight:'600', textAlign:'left'}}>
				Enter player names, then press submit. If more players required, 
				press the Plus Button to add an additional player.
			</p>
			<form onSubmit={handleSubmit} >
				{inputFields.map((item, index) => {
					return (
						<div key={index} className="form-control">
							<input
								className="add-player-form"
								maxLength="8"
								type= "text"
								name = "name"
								placeholder="Player name"
								value = {item.name}
								onChange = {(e) => handleChange(e, index)}
								required
							>
							</input>
							{inputFields.length > 1 &&
								<FaTimes 
								onClick={() => handleRemove(index)}
								style={{cursor: 'pointer'}} 
								/>
							}
						</div>
					)
				})}
				<div className='add-player-container'>
					<FcPlus 
						size='5vh'
						onClick={() => handleAdd()}
					/>
				</div>
				<div className='home-button-container'>
					<button type="submit">Submit</button>
				</div>
			</form>
		</div>
	)
}

export default AddPlayers