import {useState} from 'react'
import CreateGame from './CreateGame';
import Scoreboard from './Scoreboard';
import Results from './Results';

const Home = () => {
	const [replaySamePlayers, setReplaySamePlayers] = useState(true)
	const [showResults, setShowResults] = useState(false)
	const [gameComplete, setGameComplete] = useState(false)
	const [playersAdded, setPlayersAdded] = useState(false)
	const [createGame, setCreateGame] = useState(false)
	const [inputFields, setInputFields] = useState(
		[{
			name:"",
			ones:"",
			twos:"",
			threes:"",
			fours:"",
			fives:"",
			sixes:"",
			threeOfAKind:"",
			fourOfAKind:"",
			fullHouse:"",
			smStraight:"",
			lgStraight:"",
			yahtzee:"",
			chance:"",
			yahtzeeBonus:"",
		}]
	);

	if (showResults === true ) {
		return (
			<Results 
				inputFields={inputFields}
				setInputFields={setInputFields}
				replaySamePlayers={replaySamePlayers} 
				setReplaySamePlayers={setReplaySamePlayers}
				setShowResults={setShowResults}
				setGameComplete={setGameComplete}
				setCreateGame={setCreateGame}
				setPlayersAdded={setPlayersAdded}
			/>
		)
	} else {

	return (
		<div className="home-container">
			{!playersAdded ? 
				<CreateGame 
					setPlayersAdded={setPlayersAdded}
					inputFields={inputFields}
					setInputFields={setInputFields}
					createGame={createGame}
					setCreateGame={setCreateGame}
				/>
			:
				<Scoreboard
					inputFields={inputFields}
					setInputFields={setInputFields}
					gameComplete={gameComplete}
					setGameComplete={setGameComplete}
					setShowResults={setShowResults}
				/>
			}
		</div>
	)}
}

export default Home