import {useState} from 'react'
import ChangeScoreButtons from './ChangeScoreButtons';


const ChangeScoreDisplay = ({changeScoreDisplay, setChangeScoreDisplay, changeScoreValues, setchangeScoreValues, inputFields, setInputFields, setGameComplete}) => {

  const [scoreSelected, setScoreSelected] = useState(true)

  const checkGameComplete = () => {
    var count = 0
    let copiedValues = JSON.parse(JSON.stringify(inputFields))
    for (let i=0; i < copiedValues.length; i++) {
      let removal = copiedValues[i]
      delete removal['name']
      delete removal['yahtzeeBonus']
      var values = Object.values(removal)

      for(let i = 0; i < values.length; ++i) {
        if(values[i] === "")
          count++;
      }
    }
    if (count === 0) {
      setChangeScoreDisplay(false)
      setGameComplete(true)
    }
  }

  const handleCancel = () => {
    setChangeScoreDisplay(false);
    const changeValues = changeScoreValues
    changeValues['selectedValue'] = ''
    setchangeScoreValues(changeValues)
  }

  const handleConfirm = () => {
    const players = [...inputFields]
    players[changeScoreValues.index][`${changeScoreValues.requiredValue}`] = changeScoreValues.selectedValue
    setInputFields(players)
    const values = changeScoreValues
    values['name'] = ''
    values['titleValue'] = ''
    values['index'] = ''
    values['requiredValue'] = ''
    values['selectedValue'] = ''
    setchangeScoreValues(values)
    setChangeScoreDisplay(false)
    checkGameComplete()
  }

	return (
  	<div className="changeScore">
  		<div className="changeScore-header">
  			<p>{changeScoreValues['name']}'s Score</p>
  			<h3>{changeScoreValues['titleValue']}</h3>
  		</div>

  		<div className="changeScore-body">
        <ChangeScoreButtons 
          changeScoreValues={changeScoreValues} 
          setchangeScoreValues={setchangeScoreValues}
          scoreSelected={scoreSelected} 
          setScoreSelected={setScoreSelected}
        />
        <div>
          <button onClick={handleCancel} className="cancelScoreChangeButton">Cancel</button>
          <button disabled={scoreSelected} className="scoreChangeConfirm" onClick={handleConfirm}>Confirm</button>
        </div>
      </div>
  	</div>
	)
};

export default ChangeScoreDisplay







