
const ConfirmComplete = ({gameComplete, setGameComplete, setShowResults}) => {

  const handleConfirm = () => {
    setShowResults(true)
  }

	const handleCancel = () => {
		setGameComplete(false)
	}

	return (
		<div className="confirmComplete">
  		<div className="confirmComplete-header">
  			<h3>Confirm game is complete?</h3>
  		</div>


  		<div className="confirmComplete-body">
        <div>
        	<h4>You will be redirected to the results screen.</h4>
          <button onClick={handleCancel} className="cancelScoreChangeButton">Cancel</button>
          <button onClick={handleConfirm} className="scoreChangeConfirm">Confirm</button>
        </div>
      </div>
		</div>
	)
}

export default ConfirmComplete